@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,700;1,400&display=swap);
html,
body {
  font-family: "Roboto", sans-serif;
}

.appContent {
  min-height: 90vh;
  max-width: 90%;
}

.App {
  margin: 0;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.appContentSmall {
  margin-top: 50px;
  min-height: 75vh;
  max-width: 90%;
}
.dotClass {
    color: black;
}
.singleContent {
    display: flex;
    flex-direction: column;
    align-items: center;

}

.singleTitle {
    font-size: 2rem;
    font-weight: 700;
    color: #384955;
}

.singleImage {
    width: 80%;
    align-self: center;
}

.wordpressContent {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Roboto', sans-serif;
    font-size: 16px;
    justify-items: center;
}

.wordpressContent p {
    align-self: center;
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: center;
}

.wordpressContent video {
    align-self: center;
}

.wp-block-video {
    display: flex;
    flex-direction: column;
}

.wordWrapper {
    display: grid;
    grid-template-columns: 1fr;
    align-items: center;
    justify-items: center;
}

#dslc-theme-content {
    align-self: center;
    display: flex;
    flex-direction: column;
    align-items: center;
}

#dslc-theme-content-inner {
    max-width: 90%;
    align-self: center;
    display: flex;
    flex-direction: column;
    align-items: center;
}

#dslc-theme-content p {
    align-self: center;
}
.footer-mobile {
  display: inline-flexbox;
  background: #00adee;
  padding: 1rem 5vw;
  color: white;
  margin-top: 50px;
  width: 100vw;
}

.social-wrapper-footer-mobile {
  display: flex;
  flex-grow: 1;
  align-items: stretch;
  justify-items: center;
  font-size: large;
  width: 100%;
  height: 5vh;
}

.social-wrapper-footer-mobile a {
  color: white;
}

.social-item-mobile {
  display: flex;
  align-content: center;
  justify-content: center;
  flex-grow: 1;
  margin: 5px;
}
.footer {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr 2fr;
  background: #00adee;
  padding: 1rem 5vw;
  color: white;
  margin-top: 50px;
  width: 100vw;
}

.social-wrapper-footer {
  display: flex;
  flex-direction: row;
  justify-items: flex-end;
  align-items: center;
  font-size: xx-large;
  flex-grow: 1;
}

.social-item-first {
  margin-left: auto;
}

.social-wrapper-footer a {
  color: white;
}

.social-item {
  display: flex;
  align-content: center;
  justify-content: flex-end;
  max-width: 10%;
  padding-left: 10px;
}

.link-button {
  color: white;
  text-decoration: none;
}

.logo-wrapper {
  flex-grow: 1;
  display: flex;
  justify-items: center;
  align-items: center;
}

.link-wrapper {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}
